/* MODAL FORM */
.modalMain {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: center;
	        justify-content: center;
	text-align: center;
	position: absolute;
	left: 50%;
	top: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	width: 500px;
	height: 500px;
	background-color: white;
	border: 3px solid black;
	border-radius: 16px;
	padding: 16px;
	padding-bottom: 0px;
}
.modalMain h1 {
	margin-top: 0;
	margin-bottom: 16px;
}
.modalMain h3 {
	margin-top: 8px;
}
.modalMain img {
	margin: 8px auto;
	position: static;
	width: 30%;
}
.modalForm {
	box-sizing: border-box;
	width: 100%;
}
.modalForm :is(label, input, textarea) {
	margin: 0px auto;
	width: 80%;
	display: block;
}
.modalForm input[type='submit'] {
	margin-top: 8px;
	width: 80%;
}
.modalForm button {
	margin: 0px auto;
	display: block;
}
.modalResult {
	width: 100%;
	height: 8em;
	margin: 0 auto;
}
.modalResult textarea {
	margin: 0 auto;
	width: 80%;
	height: 100%;
}
.rolecard {
	width: 100vw;
	height: 100vh;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
}
.rolecard > * {
	padding: 4px;
	color: white;
}
.rolecard > .sidebar {
	background-color: #333;
	border-right: 2px solid black;
	width: 25%;
}
.rolecard > .content {
	-webkit-flex-grow: 1;
	        flex-grow: 1;
}
.rolecard > .content > .component {
	background-color: white;
	color: black;
	border: 1px solid black;
	width: 100%;
	padding: 8px;
}
.component > form {
	width: 80%;
	display: block;
	margin: 0 auto;
}
textarea {
	height: 100px;
	width: 100%;
}
.rolecard > .sidebar > .sidebarButton {
	width: 100%;
	border: 1px solid white;
	text-align: center;
	user-select: none; /* supported by Chrome and Opera */
	-webkit-user-select: none; /* Safari */
	-khtml-user-select: none; /* Konqueror HTML */
	-moz-user-select: none; /* Firefox */
	-ms-user-select: none; /* Internet Explorer/Edge */
}

.rolecard > .sidebar > .selected {
	color: black;
	background-color: white;
	border: 1px solid black;
}

@media (max-width: 600px) {
	.modalMain {
		width: 85vw;
	}
}

